.app-area {
    width: 100%;
    height: 95%;
    position: absolute;
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
}

.centerize {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

.p-absolute {
    position: absolute;
}

.menu-main {
    width: 85%;
    height: 80%;
    background: #ffffff;
    background-color: rgba(255, 255, 255, 0.3);
    border-radius: 15px;
}

.menu-main-1 {
    opacity: 1;
}

.menu-header {
    width: 100%;
    height: 5%;
    display: block;
}

.close-icon {
    border-radius: 20px;
    width: 20px;
}

.menu-body {
    width: 95%;
    height: 95%;
    /* display: flex;
    justify-content: space-evenly; */
    padding: 5%;
    overflow: auto;
}

.dialog-desktop {
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: 1;
    background: transparent;
    display: flex;
    justify-content: center;
    align-items: center
}

.icon-area {
    width: 7%;
    float: left
}

.term-container { 
    width: 60%;
    height: 60vh; 
    background: #000; 
    display: flex;
    justify-content: center; 
    align-items: center;
    flex-direction: column; 
    color: #fff;
}

.term-history { 
    width: 95%; 
    height: 90%; 
    overflow: auto; 
    padding: 1%;
    display: flex;
    flex-direction: column;
}

.term-prompt {
    background: transparent;
    width: 90%;
    height: 100%;
    color: #ffffff;
    border: none;
}