.desktop-main {
    overflow: hidden;
    width: 100%;
    height: 100vh;
    position: absolute;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.desktop-bg {
    width: 100%;
    height: 95%;
    display: flex;
    justify-content: flex-start;
}

.taskbar-bg {
    width: 100%;
    height: 5%;
    background-color: #fff;
    /* border: 0.1px solid #000; */
    background-color: rgba(0,0,0,0.3);
    display: flex;
    justify-content: center;
    align-items: center;
}

.icon {
    background-color: rgba(128,128,128,0);
    color: #fff;
    width: 2%;
    height: 90%;
    padding: 5px;
    border-radius: 15%;
}

/* Ripple effect */
.ripple {
    background-position: center;
    transition: background 0.8s;
  }
  .ripple:hover {
    background: #47a7f5 radial-gradient(circle, transparent 1%, #47a7f5 1%) center/15000%;
    transform: scale(150%);
  }
  .ripple:active {
    background-color: #6eb9f7;
    background-size: 100%;
    transition: background 0s;
  }

  .desktop-bg-icons {
    width: 50%;
    height: 100%;
    padding: 1%;
  }

  #draggable {
    font-weight: 600;
  }

  .desktop-icon {
    background-color: rgba(128,128,128,0);
    color: #fff;
    width: 2%;
    padding: 5px;
    border-radius: 15%;
  }
